import React from "react/addons"
import RouterStore from "root/stores/logged-out-router-store"
import Welcome from "root/pages/logged-out/welcome"

var LoggedOut = React.createClass({

  mixins: [RouterStore.mixin],

  getStateFromStores: function () {
    return {
      reactComponent: RouterStore.getReactComponent(),
      reactComponentProps: RouterStore.getReactComponentProps()
    }
  },

  render: function () {
    var Component = this.state.reactComponent
    return <div>
      <Component {...this.state.reactComponentProps} />
    </div>
  }

})

export default LoggedOut



