import React from "react/addons"
import LoggedOutRouterActionCreator from "root/action-creators/logged-out-router-action-creator"

var Welcome = React.createClass({

  onLogin: function(event) {
    event.preventDefault()
    LoggedOutRouterActionCreator.navigateToLogin()
  },

  onRegister: function(event) {
    event.preventDefault()
    LoggedOutRouterActionCreator.navigateToRegister()
  },

  render: function() {
    return <div className="container" >
      <div className="row">
        <h1>Welcome to SGPP!</h1>
      </div>
      <div className="row">
        <button type="submit" className="col s12 waves-effect waves-light btn" onClick={ this.onLogin }  >Login</button>
      </div>
      <div className="row">
        <button type="submit" className="col s12 waves-effect waves-light btn" onClick={ this.onRegister } >Register</button>
      </div>
    </div>
  }

})

export default Welcome



