import React from "react/addons"
import TodoList from "root/widgets/todo-list"
import todosActionCreator from "root/action-creators/todos-action-creator"

var TodoApp = React.createClass({

  mixins: [React.addons.LinkedStateMixin],

  getInitialState: function () {
    return { text: "" }
  },

  onSubmit: function(event, arg2) {
    event.preventDefault()

    if (this.state.text && this.state.text.trim().length !== 0) {
      todosActionCreator.addTodo({ text: this.state.text })
      this.setState({text: ""})
    }
  },

  render: function() {
    return <div>
      <TodoList todos={ this.props.todos } />
      <form onSubmit={ this.onSubmit }>
        <input valueLink={ this.linkState('text') } />
        <button>{ `Add (${this.props.todos.size})` }</button>
      </form>
    </div>
  }

})

export default TodoApp



