import React from "react/addons"
import AuthStore from "root/stores/auth-store"
import AuthActionCreator from "root/action-creators/auth-action-creator"

import "./get-credentials.sass"

var GetCredentials = React.createClass({

  mixins: [AuthStore.mixin],

  getStateFromStores: function () {
    return {
      emailErrorMessage: AuthStore.getEmailErrorMessage(),
      passwordErrorMessage: AuthStore.getPasswordErrorMessage()
    }
  },

  getInitialState: function() {
    return {
      email: "",
      password: ""
    }
  },

  onEmailChange: function(event) {
    AuthActionCreator.clearErrors()
    this.setState({ email: event.target.value })
  },

  onPasswordChange: function(event) {
    AuthActionCreator.clearErrors()
    this.setState({ password: event.target.value })
  },

  renderErrorMessage: function() {
    if (this.state.emailErrorMessage || this.state.passwordErrorMessage) {
      return <p>
        { this.state.emailErrorMessage || this.state.passwordErrorMessage }
      </p>
    }
  },

  onSubmit: function (event) {
    event.preventDefault()
    this.props.onSubmit(this.state.email, this.state.password)
  },

  render: function() {
    var emailClasses = this.state.emailErrorMessage ? "invalid" : ""
    var passwordClasses = this.state.passwordErrorMessage ? "invalid" : ""
    return <div className="get-credentials" >
      <div className="row">
        { this.renderErrorMessage() }
      </div>
      <form onSubmit={ this.onSubmit }>
        <div className="row">
          <input type="email" placeholder="Email" className={ emailClasses } onChange={ this.onEmailChange } />
          <input type="password" placeholder="Password" className={ passwordClasses } onChange={ this.onPasswordChange } />
        </div>
        <div className="row">
          <button type="submit" className="col s12 waves-effect waves-light btn" >{ this.props.submitString }</button>
        </div>
      </form>
    </div>
  }

})

export default GetCredentials



