import ImmutableStore from 'fluxthis/src/ImmutableStore.es6'
import Firebase from 'firebase'
import TodosActionCreator from "root/action-creators/todos-action-creator"
import FirebaseHelper from "root/util/firebase-helper"

var firebaseTodosRef

var onTodosValue = function (newTodosObject) {
  var updatedTodos = FirebaseHelper.toArray(newTodosObject.val())
  setTimeout(function () {
    TodosActionCreator.setTodos(updatedTodos)
  }, 0)
}

var TodosStore = new ImmutableStore({

  displayName: 'TodosStore',

  init: function (options) {
    this.todos = ImmutableStore.Immutable.List()
    this.bindActions(
      'ADD_TODO', this.requestAddTodo,
      'SET_TODOS', this.setTodos,
      'SET_UID', this.setUid
    )
  },

  public: {
    getTodos: function () {
      return this.todos
    }
  },

  private: {
    requestAddTodo: function (todo) {
      firebaseTodosRef.push(todo)
    },

    setUid: function (uid) {
      if(firebaseTodosRef) {
        firebaseTodosRef.off('child_added', onTodosValue)
      }

      if(uid) {
        firebaseTodosRef = new Firebase(
          `${FirebaseHelper.appUrl}/users/${uid}/todos/`
        )
        firebaseTodosRef.on('value', onTodosValue)
      } else {
        firebaseTodosRef = null
        this.todos = ImmutableStore.Immutable.List()
      }
    },

    setTodos: function (newTodos) {
      this.todos = ImmutableStore.Immutable.List(newTodos)
    }

  }

})

export default TodosStore



