import React from "react/addons"

var TodoList = React.createClass({
  renderTodo: function(todo, index) {
    return <li key={ index }>{ todo.text }</li>;
  },

  render: function() {
    return <ul>{ this.props.todos.map(this.renderTodo) }</ul>;
  }
});

export default TodoList



