import React from "react/addons"
import AuthActionCreator from "root/action-creators/auth-action-creator"
import LoggedOutRouterActionCreator from "root/action-creators/logged-out-router-action-creator"
import GetCredentials from "root/pages/logged-out/get-credentials"

var Register = React.createClass({

  onLogin: function () {
    LoggedOutRouterActionCreator.navigateToLogin()
  },

  onRegister: function (email, password) {
    AuthActionCreator.register({ email, password })
  },

  onWelcome: function () {
    LoggedOutRouterActionCreator.navigateToWelcome()
  },

  render: function () {
    return <div className="container">
      <div className="row">
        <h3>Register</h3>
      </div>
      <GetCredentials onSubmit={ this.onRegister } submitString="Register" />
      <div className="row">
        <button onClick={ this.onLogin } className="col s12 waves-effect waves-light btn-flat" >Already registered? Login</button>
        <button onClick={ this.onWelcome } className="col s12 waves-effect waves-light btn-flat" >Back to Welcome</button>
      </div>
    </div>
  }

})

export default Register



