import ActionCreator from 'fluxthis/src/ActionCreator.es6'

var TodosActionCreator =  new ActionCreator({

  displayName: 'TodosActionCreator',

  clearTodos: {
    type: 'CLEAR_TODOS'
  },

  setTodos: {
    type: 'SET_TODOS',
    payload: ActionCreator.PayloadTypes.array.isRequired
  },

  addTodo: {
    type: 'ADD_TODO',
    payload: ActionCreator.PayloadTypes.shape({
      text: ActionCreator.PayloadTypes.string.required
    }).isRequired
  }

})

export default TodosActionCreator



