import ObjectOrientedStore from 'fluxthis/src/ObjectOrientedStore.es6'

import AuthActionCreator from "root/action-creators/auth-action-creator"

import Login from "root/pages/logged-out/login"
import Register from "root/pages/logged-out/register"
import Welcome from "root/pages/logged-out/welcome"

var LoggedOutRouterStore = new ObjectOrientedStore({

  displayName: 'LoggedOutRouterStore',

  init: function (options) {
    this.reactComponent = Welcome
    this.reactComponentProps = {}

    this.bindActions(
      'NAVIGATE_TO_LOGIN', this.navigateToLogin,
      'NAVIGATE_TO_REGISTER', this.navigateToRegister,
      'NAVIGATE_TO_WELCOME', this.navigateToWelcome,

      'SET_UID', this.navigateToWelcome
    )
  },

  public: {
    getReactComponent: function () {
      return this.reactComponent
    },

    getReactComponentProps: function () {
      return this.reactComponentProps
    }
  },

  private: {
    navigateToLogin: function () {
      this.reactComponent = Login
    },

    navigateToRegister: function () {
      this.reactComponent = Register
    },

    navigateToWelcome: function () {
      this.reactComponent = Welcome
    }
  }

})

export default LoggedOutRouterStore



