import React from "react/addons"
import injectTapEventPlugin from "react-tap-event-plugin"

import LoggedIn from "root/pages/logged-in"
import LoggedOut from "root/pages/logged-out"

import AuthStore from "root/stores/auth-store"

// Can go away when react 1.0 release
injectTapEventPlugin()

var App = React.createClass({

  mixins: [AuthStore.mixin],

  getStateFromStores: function () {
    return {
      uid: AuthStore.getUid()
    }
  },

  render: function () {
    var Component = this.state.uid ? LoggedIn : LoggedOut
    return <Component />
  }

})

React.render(<App/>, document.getElementById("app"))



