import ActionCreator from 'fluxthis/src/ActionCreator.es6'

var LoggedOutRouterActionCreator =  new ActionCreator({

  displayName: 'LoggedOutRouterActionCreator',

  navigateToLogin: {
    type: 'NAVIGATE_TO_LOGIN'
  },

  navigateToRegister: {
    type: 'NAVIGATE_TO_REGISTER'
  },

  navigateToWelcome: {
    type: 'NAVIGATE_TO_WELCOME'
  }

})

export default LoggedOutRouterActionCreator



